<section class="mb-5">
  <h3>緣起</h3>
  <hr>
  <p>自2020年5月起，本網站彙整國內人口、性別、與選舉相關之政府開放資料連結，除直接造表提供連結外，並整理跨資料集的彙整檔案，以 CSV / JSON 等檔案格式發布，便於研究與課程進行探究。除資料提供外，本網站提供資料分析與視覺化的報告供外界查閱與下載，內容包含前述彙整檔案的地理視覺化結果，以及部分中心研究人員所產製的資料分析報告，涵蓋選舉、社會福利、平等等與人口統計與性別相關之議題。</p>
</section>
