<section class="authorize">
  <h3>內容授權</h3>
  <hr>
  <p >本網站內容授權分四種類型：</p>
  <ol class="content-author">
    <li>政府開放資料連結的規範依照所連結的資料集說明，本網站僅提供連結。</li>
    <li>資料彙整的檔案下載需在衍生品上依創用CC以標示姓名「人口與性別研究中心」，得修改內容並進行發表，並以「台大人口與性別研究中心（2020）。人口統計與公投資料集。XX/XX/XX 取自______」（範例）的方式引用。</li>
    <li>互動地圖以創用CC姓名標註禁止改作的方式授權。僅可下載截圖，不得複製網站程式碼作為個人或商業、非商業用途使用。</li>
    <li>案例分析各項以創用CC姓名標註禁止改作的方式授權。僅可引用，不得擷取部分成果作為個人或商業、非商業用途使用。以「台大人口與性別研究中心（2020）。人口統計與產假支薪分析報告。XX/XX/XX 取自______」（範例）的方式引用。</li>
  </ol>
</section>

