<section class="mb-5">
  <h3>人口、性別與選舉資料彙整</h3>
  <hr />
  <p>
    本中心將相關政府部會的人口、性別與選舉統計資料清理彙整後，依據不同目的提供彙整後的資料讓研究者或課程進行下載。惟資料彙整的檔案下載需在衍生品上依創用CC以標示姓名「人口與性別研究中心」，得修改內容並進行發表，並以「台大人口與性別研究中心（2020）。人口統計與公投資料集。XX/XX/XX
    取自______」（範例）的方式引用。<img
      src="http://digiarch.sinica.edu.tw/images/a/auth/au034.png"
      alt=""
      width="30"
    />
  </p>
</section>
<section class="mb-5">
