<script>
import { onMount } from 'svelte'; 

export let dataTables = {
  govtw: [],
  gendertw: [],
  electiontw: [],
  genderint: []
};

onMount(() => {
  // // Fetch data from Google Sheet API 
  const apiUrl = 'https://script.google.com/macros/s/AKfycbzjQTO_sI-TFhjih0kFC5jhn6JZdan7bJtBumJ4ow/exec'
  const getData = async url => {
    try {
      const res = await fetch(url)
      const data = await res.text()
      // console.log(data)

      // Clear data format
      const tableGroup = data.split('|')
      tableGroup.forEach(table => {
        const rowArr = table.split(';')
                            .map(item => item.split(',,'))
                            .filter(item => item[0] !== '')
        const tableName = rowArr.shift()[0]
        dataTables[tableName] = rowArr
      })
    } catch(err) {
      console.log(err)
    }
  }
  getData(apiUrl)
})
</script>

<section class="mb-5">
  <h3>人口、性別與選舉相關開放資料匯集</h3>
  <hr>
  <p>本頁面僅提供人口、性別與選舉相關政府開放資料集的連結收集，資料涵蓋範圍、內容與使用規範由政府開放資料提供端維護，請查閱所連結之資料庫說明。惟本網頁所整理之內容，不得直接複製作為其他資料集使用。</p>
</section>
